<template>
  <div id="app">
    <div id="loading" class="nookstamp-loading">
      <div class="nookstamp-loading__logo">
        <img v-if="logo" :src="logo" />

        <ul class="nookstamp-loading__dot">
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </div>
    </div>
    <router-view />
  </div>
</template>

<script>
import logo from "@/assets/logo.png";
export default {
  data() {
    return {
      logo
    };
  },
  mounted() {
    const self = this;
    setTimeout(function() {
      var el = document.querySelector("#loading");
      self.fadeOut(el);
    }, 2000);

    const body = document.body;
    this.timer;
    window.addEventListener("scroll", () => {
      clearTimeout(this.timer);
      if (!body.classList.contains("avoid-pointer")) {
        body.classList.add("avoid-pointer");
      }

      this.timer = setTimeout(() => {
        body.classList.remove("avoid-pointer");
      }, 500);
    });
  },
  methods: {
    fadeOut(el) {
      el.style.opacity = 1;

      (function fade() {
        if ((el.style.opacity -= 0.1) < 0) {
          el.style.display = "none";
        } else {
          requestAnimationFrame(fade);
        }
      })();
    }
  }
};
</script>

<style lang="scss">
[v-cloak] {
  display: none;
}

.avoid-pointer {
  pointer-events: none;
}
</style>
