import Vue from 'vue'
import Vuex from 'vuex'
import datas from '@/output.js'
import Codes from '../utils/codes'
import { objClone } from '@/utils'
Vue.use(Vuex)
const tokenKey = 'nook-stamp'

var fillZeroArray = new Array(datas.saveOrder.length).fill(0)
// var fillZeroArray=new Array(parseInt(datas.saveOrder.length/2)).fill(0).concat(new Array(parseInt(datas.saveOrder.length/2)).fill(1))

const state = {
  listDatas: datas.results,
  status: fillZeroArray,
  term: '',
  filterType: 'all',
  hasStoredStatus: !!localStorage.getItem(tokenKey),
}
const mutations = {
  UPDATE_FILTER_TYPE: (state, data) => {
    state.filterType = data
  },
  UPDATE_STATUS: (state, data) => {
    state.status = [...data]
  },
  LOAD_STATUS: (state) => {
    const data = localStorage.getItem(tokenKey)
    var status
    if (data) {
      status = Codes.importFromHash(data)
      state.status = status.split('').map((s) => parseInt(s, 10))
    }
  },
  READ_STATUS: (state, data) => {
    var status
    if (data) {
      status = Codes.importFromHash(data)
      state.status = status.split('').map((s) => parseInt(s, 10))
    } else {
      const data = localStorage.getItem(tokenKey)
      if (data) {
        state.status = JSON.parse(data)
      }
    }
  },
  SAVE_STATUS: (state, data) => {
    localStorage.setItem(tokenKey, data)
  },
  UPDATE_TERM: (state, term) => {
    state.term = term
  }
}



const actions = {


}


const getters = {
  filterListDatas: state => {
    const fuzzFilter = (item) => {
      var hay = item.zhName.toLowerCase()
      var i = 0
      var n = -1
      var l
      var s = state.term.toLowerCase()
      for (; (l = s[i++]);) if (!~(n = hay.indexOf(l, n + 1))) return false
      return item.variations.length > 0
    }

    if (state.term === '' && state.filterType === 'all') {
      return state.listDatas
    } else if (state.filterType === 'all') {
      return state.listDatas.filter(fuzzFilter);
    } else {
      return state.listDatas.map(item => {
        const factor = state.filterType === 'owned' ? 1 : 0
        const variationsClone = [...item.variations]
        const filterResult = variationsClone.filter(variation => state.status[variation.idx] === factor)
        return {
          ...item,
          variations: filterResult
        }
      }).filter(fuzzFilter)
    }
  },
  variationCount: state => {
    const ownedArray = state.status.filter(function (it) { return it == 1 })
    return {
      all: state.status.length,
      owned: ownedArray.length
    }
  }
}

const store = new Vuex.Store({
  state,
  actions,
  mutations,
  getters

})

export default store


