import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/:hash?',
    name: 'book',
    component: () => import('@/views/book/index.vue')
  },
  {
    path: '/f/icons',
    name: 'icon',
    component: () => import('@/views/icons/index.vue')
  }
]

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
})

export default router
